.navbar{
  position: fixed;
  background: #000;
  color: yellow;
  line-height: 30px;
  padding: 10px;
  overflow: hidden;
  width: 100%;
  text-align: center;
  font-size: 25px;
  font-family: 'Times New Roman', Times, serif;
  
}

.notes {
  position: relative;
  display: inline-block;
}

.scoreDisplay {
  position: relative;
  float: right;
}

.title {
  position: relative;
  float: left;
}

img {
  border: 1px solid #000;
  height:160px;
  width: 160px;
  margin: 0px 10px 25px 10px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
div.img-container {
  float: left;
  width: 25%;
  padding: 5px;
}
div.card-container {
    margin:150px auto;
    max-width: 960px;
    width:100%;
    text-align: center;
}
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

